/**
 * Site header
 */
.wrapper .container {
  max-width: 80rem;
}

.site-header {
  text-align: center;
}

.navbar {
  display: block;
}

.nav-item {
  display: inline-block;
  padding: 1rem;
}

.post-nav {
  text-align: center;
  padding: 1rem;
}

.post-title {
  display: block;
}

/**
 * override column size for mobile
 */
@media (max-width: 40rem) {
  .row .column.column-33,
  .row .column.column-34 {
    flex: 0 0 100%;
    max-width: 100%;
  }
}

/**
 * Site footer
 */
.site-footer {
  border-top: 1px solid $grey-color-light;
  margin: 2rem 0;
  padding: 2rem 0;
}

.contact-list,
.social-media-list {
  list-style: none;
  margin-left: 0;
}

/**
 * Misc
 */
.preview-panel {
  padding: 1rem;
  box-sizing: border-box;
  border: 1px solid #fabcc7;

  &:hover {
    border: 1px solid $grey-color-light;
  }
}
